import axios from 'axios';

const isDevelopment = process.env.NODE_ENV === 'development';

// Mock data for development
const mockData = {
  user: {
    id: 'mock-user-id',
    username: 'Mock User',
    email: 'mock@example.com',
    points: 500,
    role: 'admin',
    lastTeaserSolved: null as string | null,
  },
  leaderboard: [
    { id: '1', username: 'User 1', points: 500, rank: 1 },
    { id: '2', username: 'User 2', points: 450, rank: 2 },
    { id: '3', username: 'User 3', points: 400, rank: 3 },
  ],
  teasers: [
    {
      id: '1',
      question: 'Mock Teaser 1',
      answer: 'mock',
      difficulty: 'easy',
      points: 100,
      date: new Date().toISOString().split('T')[0],
    },
    {
      id: '2',
      question: 'Previous Day Teaser',
      answer: 'previous',
      difficulty: 'medium',
      points: 150,
      date: new Date(Date.now() - 24 * 60 * 60 * 1000).toISOString().split('T')[0],
    },
  ],
  gameState: {
    totalPoints: 500,
    level: 1,
    creaturesFound: 2,
    creaturesRemaining: 3,
  },
  settings: {
    creatureAttackInterval: 60,
    pointsPerSolve: 100,
    streakBonus: 0.5,
    enigmaticPeaceThreshold: 10,
    isEnigmaticPeaceEnabled: true,
    isCreatureAttacksEnabled: true,
    maxDailyTeasers: 1,
    maintenanceMode: false,
  },
  userStats: {
    totalSolved: 25,
    currentStreak: 7,
    longestStreak: 14,
    rank: 3,
    points: 500,
  },
};

const api = axios.create({
  baseURL: process.env.REACT_APP_API_URL || 'http://localhost:5001/api',
  headers: {
    'Content-Type': 'application/json',
  },
});

// Request interceptor for adding auth token
api.interceptors.request.use(
  (config) => {
    const token = localStorage.getItem('token');
    if (token) {
      config.headers.Authorization = `Bearer ${token}`;
    }
    return config;
  },
  (error) => {
    return Promise.reject(error);
  }
);

// Response interceptor for handling token expiration and development mode
api.interceptors.response.use(
  (response) => response,
  (error) => {
    if (error.response?.status === 401) {
      localStorage.removeItem('token');
      window.location.href = '/login';
    }
    
    // In development mode, return mock data instead of errors
    if (isDevelopment && error.config) {
      const url = error.config.url || '';
      if (url.includes('/users/profile')) {
        return Promise.resolve({ data: mockData.user });
      }
      if (url.includes('/users/leaderboard')) {
        return Promise.resolve({ data: mockData.leaderboard });
      }
      if (url.includes('/brain-teasers')) {
        return Promise.resolve({ data: mockData.teasers });
      }
      if (url.includes('/game-state')) {
        return Promise.resolve({ data: mockData.gameState });
      }
      if (url.includes('/admin/settings')) {
        if (error.config.method === 'get') {
          return Promise.resolve({ data: mockData.settings });
        } else if (error.config.method === 'put') {
          mockData.settings = { ...mockData.settings, ...error.config.data };
          return Promise.resolve({ data: mockData.settings });
        }
      }
      if (url.includes('/users/stats')) {
        return Promise.resolve({ data: mockData.userStats });
      }
    }
    
    return Promise.reject(error);
  }
);

export const auth = {
  register: (data: { username: string; email: string; password: string }) =>
    isDevelopment
      ? Promise.resolve({ data: { token: 'mock-token', user: mockData.user } })
      : api.post('/users/register', data),
      
  login: (data: { email: string; password: string }) =>
    isDevelopment
      ? Promise.resolve({ data: { token: 'mock-token', user: mockData.user } })
      : api.post('/users/login', data),
      
  getProfile: () =>
    isDevelopment
      ? Promise.resolve({ data: mockData.user })
      : api.get('/users/profile'),
      
  updateProfile: (data: { username?: string; email?: string; password?: string }) =>
    isDevelopment
      ? Promise.resolve({ data: { user: { ...mockData.user, ...data } } })
      : api.patch('/users/profile', data),
      
  getLeaderboard: () =>
    isDevelopment
      ? Promise.resolve({ data: mockData.leaderboard })
      : api.get('/users/leaderboard'),
};

export const brainTeasers = {
  getTodayTeaser: () => {
    if (isDevelopment) {
      const today = new Date().toISOString().split('T')[0];
      if (mockData.user.lastTeaserSolved === today) {
        return Promise.resolve({ 
          data: { 
            alreadySolved: true,
            message: 'You have already solved today\'s brain teaser. Come back tomorrow!',
            nextTeaserDate: new Date(Date.now() + 24 * 60 * 60 * 1000).toISOString().split('T')[0],
            previousTeaser: mockData.teasers[1]
          } 
        });
      }
      return Promise.resolve({ data: mockData.teasers[0] });
    }
    return api.get('/brain-teasers/today');
  },
      
  submitSolution: (data: { teaserId: string; solution: string }) => {
    if (isDevelopment) {
      const today = new Date().toISOString().split('T')[0];
      mockData.user.lastTeaserSolved = today;
      mockData.user.points += 100;
      return Promise.resolve({ 
        data: { 
          success: true, 
          points: 100,
          message: 'Congratulations! You solved today\'s brain teaser.',
          nextTeaserDate: new Date(Date.now() + 24 * 60 * 60 * 1000).toISOString().split('T')[0]
        } 
      });
    }
    return api.post('/brain-teasers/submit', data);
  },
      
  getAllTeasers: () =>
    isDevelopment
      ? Promise.resolve({ data: mockData.teasers })
      : api.get('/brain-teasers/all'),
      
  createTeaser: (data: {
    title: string;
    description: string;
    solution: string;
    difficulty: 'easy' | 'medium' | 'hard';
    publishDate: string;
    imageUrl?: string;
  }) =>
    isDevelopment
      ? Promise.resolve({ data: { ...data, id: 'mock-id' } })
      : api.post('/brain-teasers', data),
      
  updateTeaser: (id: string, data: any) =>
    isDevelopment
      ? Promise.resolve({ data: { id, ...data } })
      : api.patch(`/brain-teasers/${id}`, data),
      
  deleteTeaser: (id: string) =>
    isDevelopment
      ? Promise.resolve({ data: { success: true } })
      : api.delete(`/brain-teasers/${id}`),
      
  generateTeaser: (data: {
    prompt: string;
    difficulty: 'easy' | 'medium' | 'hard';
  }) =>
    isDevelopment
      ? Promise.resolve({ data: mockData.teasers[0] })
      : api.post('/brain-teasers/generate', data),
};

export const gameState = {
  getGameState: () =>
    isDevelopment
      ? Promise.resolve({ data: mockData.gameState })
      : api.get('/game-state'),
      
  checkCreatureAttacks: () =>
    isDevelopment
      ? Promise.resolve({ data: { attacked: false } })
      : api.post('/game-state/check-attacks'),
      
  getCommunityProgress: () =>
    isDevelopment
      ? Promise.resolve({ data: mockData.gameState })
      : api.get('/game-state/progress'),
};

export default api; 