import React, { useState } from 'react';
import { useNavigate, useLocation } from 'react-router-dom';
import {
  AppBar,
  Box,
  IconButton,
  Toolbar,
  Typography,
  useMediaQuery,
  useTheme,
  Container,
  Menu,
  MenuItem,
  Button,
  Stack,
  Tooltip,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField,
} from '@mui/material';
import {
  Menu as MenuIcon,
  Home as HomeIcon,
  Person as PersonIcon,
  EmojiEvents as LeaderboardIcon,
  Extension as MazeIcon,
  AdminPanelSettings as AdminIcon,
  Lightbulb as DailyIcon,
  Settings as SettingsIcon,
  BugReport as DebugIcon,
} from '@mui/icons-material';
import { motion, AnimatePresence } from 'framer-motion';
import { styled } from '@mui/material/styles';
import { useAuth } from '../contexts/AuthContext';

const StyledAppBar = styled(AppBar)(({ theme }) => ({
  background: 'rgba(18, 18, 18, 0.6)',
  backdropFilter: 'blur(8px)',
  boxShadow: 'none',
  borderBottom: `1px solid rgba(255, 255, 255, 0.1)`,
}));

const AppTitle = styled(Typography)(({ theme }) => ({
  fontFamily: 'Almendra, serif',
  fontSize: '2rem',
  fontWeight: 400,
  letterSpacing: '1px',
  background: `linear-gradient(135deg, ${theme.palette.primary.main}, ${theme.palette.secondary.main})`,
  WebkitBackgroundClip: 'text',
  WebkitTextFillColor: 'transparent',
  cursor: 'pointer',
  transition: 'opacity 0.2s ease-in-out',
  '&:hover': {
    opacity: 0.8,
  },
}));

const NavButton = styled(Button)(({ theme }) => ({
  fontFamily: 'Almendra, serif',
  color: theme.palette.text.primary,
  padding: theme.spacing(1, 2),
  borderRadius: theme.shape.borderRadius,
  transition: 'all 0.2s ease-in-out',
  '&:hover': {
    background: theme.palette.action.hover,
    transform: 'translateY(-2px)',
  },
  '&.active': {
    background: theme.palette.primary.main,
    color: theme.palette.primary.contrastText,
  },
}));

const SignInButton = styled(NavButton)(({ theme }) => ({
  background: `linear-gradient(135deg, ${theme.palette.primary.main}, ${theme.palette.secondary.main})`,
  color: theme.palette.primary.contrastText,
  '&:hover': {
    background: `linear-gradient(135deg, ${theme.palette.primary.dark}, ${theme.palette.secondary.dark})`,
    transform: 'translateY(-2px)',
  },
}));

const DebugButton = styled(IconButton)(({ theme }) => ({
  position: 'fixed',
  bottom: theme.spacing(2),
  right: theme.spacing(2),
  background: 'rgba(30, 30, 30, 0.8)',
  backdropFilter: 'blur(4px)',
  border: '1px solid rgba(255, 255, 255, 0.1)',
  '&:hover': {
    background: 'rgba(30, 30, 30, 0.9)',
  },
}));

const menuItems = [
  { text: 'Daily', icon: <DailyIcon />, path: '/daily', auth: true },
  { text: 'Maze', icon: <MazeIcon />, path: '/maze', auth: true },
  { text: 'Leaderboard', icon: <LeaderboardIcon />, path: '/leaderboard', auth: true },
  { text: 'Profile', icon: <PersonIcon />, path: '/profile', auth: true },
];

const Layout: React.FC<{ children: React.ReactNode }> = ({ children }) => {
  const [mobileMenuAnchor, setMobileMenuAnchor] = useState<null | HTMLElement>(null);
  const [debugOpen, setDebugOpen] = useState(false);
  const [debugRole, setDebugRole] = useState<'user' | 'admin'>('user');
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('md'));
  const navigate = useNavigate();
  const location = useLocation();
  const { user, login, logout, setUser } = useAuth();
  const isDevelopment = process.env.NODE_ENV === 'development';

  const handleMobileMenuOpen = (event: React.MouseEvent<HTMLElement>) => {
    setMobileMenuAnchor(event.currentTarget);
  };

  const handleMobileMenuClose = () => {
    setMobileMenuAnchor(null);
  };

  const handleNavigation = (path: string) => {
    navigate(path);
    handleMobileMenuClose();
  };

  const renderNavItems = () => {
    return menuItems
      .filter((item) => !item.auth || (item.auth && user))
      .map((item) => {
        const isActive = location.pathname === item.path;
        return (
          <NavButton
            key={item.text}
            onClick={() => handleNavigation(item.path)}
            className={isActive ? 'active' : ''}
            startIcon={item.icon}
          >
            {item.text}
          </NavButton>
        );
      });
  };

  const handleDebugLogin = async () => {
    try {
      // Create a mock user object with all required fields
      const mockUser = {
        id: 'debug-user-id',
        email: 'debug@example.com',
        role: debugRole,
        username: `Debug ${debugRole}`,
        points: debugRole === 'admin' ? 9999 : 500,
      };
      
      // Set the user state
      setUser(mockUser);
      console.log(`Debug: Logged in as ${debugRole}`, mockUser);
      setDebugOpen(false);
    } catch (error) {
      console.error('Debug login failed:', error);
    }
  };

  const handleDebugLogout = async () => {
    try {
      // Clear user state
      setUser(null);
      console.log('Debug: Logged out');
      setDebugOpen(false);
    } catch (error) {
      console.error('Debug logout failed:', error);
    }
  };

  return (
    <Box sx={{ display: 'flex', flexDirection: 'column', minHeight: '100vh' }}>
      <StyledAppBar position="fixed">
        <Toolbar>
          <AppTitle
            onClick={() => navigate('/')}
            sx={{ flexGrow: 1 }}
          >
            Flummoxed
          </AppTitle>

          {isMobile ? (
            <>
              <Box sx={{ flexGrow: 1 }} />
              <IconButton
                color="inherit"
                aria-label="menu"
                onClick={handleMobileMenuOpen}
                edge="end"
              >
                <MenuIcon />
              </IconButton>
              <Menu
                anchorEl={mobileMenuAnchor}
                open={Boolean(mobileMenuAnchor)}
                onClose={handleMobileMenuClose}
                PaperProps={{
                  sx: {
                    background: 'rgba(18, 18, 18, 0.9)',
                    backdropFilter: 'blur(8px)',
                    mt: 1,
                  },
                }}
              >
                {menuItems
                  .filter((item) => !item.auth || (item.auth && user))
                  .map((item) => (
                    <MenuItem
                      key={item.text}
                      onClick={() => handleNavigation(item.path)}
                      selected={location.pathname === item.path}
                    >
                      <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                        {item.icon}
                        {item.text}
                      </Box>
                    </MenuItem>
                  ))}
                {!user && (
                  <MenuItem onClick={() => handleNavigation('/login')}>
                    <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                      <PersonIcon />
                      Sign In
                    </Box>
                  </MenuItem>
                )}
              </Menu>
            </>
          ) : (
            <Stack direction="row" spacing={1}>
              {renderNavItems()}
              {!user && (
                <Button
                  variant="contained"
                  color="primary"
                  onClick={() => handleNavigation('/login')}
                >
                  Sign In
                </Button>
              )}
            </Stack>
          )}
        </Toolbar>
      </StyledAppBar>

      <Box
        component="main"
        sx={{
          flexGrow: 1,
          p: 3,
          mt: '56px',
        }}
      >
        <Container maxWidth="lg">
          <AnimatePresence mode="wait">
            <motion.div
              key={location.pathname}
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              exit={{ opacity: 0, y: -20 }}
              transition={{ duration: 0.3 }}
            >
              {children}
            </motion.div>
          </AnimatePresence>
        </Container>
      </Box>

      {/* Debug Button - Only visible in development */}
      {isDevelopment && (
        <Tooltip title="Debug Mode">
          <DebugButton
            onClick={() => setDebugOpen(true)}
            color="inherit"
            size="large"
          >
            <DebugIcon />
          </DebugButton>
        </Tooltip>
      )}

      {/* Debug Dialog - Only visible in development */}
      {isDevelopment && (
        <Dialog 
          open={debugOpen} 
          onClose={() => setDebugOpen(false)}
          PaperProps={{
            sx: {
              background: 'rgba(30, 30, 30, 0.9)',
              backdropFilter: 'blur(10px)',
              border: '1px solid rgba(255, 255, 255, 0.1)',
            },
          }}
        >
          <DialogTitle>Debug Mode</DialogTitle>
          <DialogContent>
            <Stack spacing={2} sx={{ mt: 1 }}>
              <Box>
                <Typography variant="subtitle2" gutterBottom>
                  Select Role
                </Typography>
                <Stack direction="row" spacing={1}>
                  <Button
                    variant={debugRole === 'user' ? 'contained' : 'outlined'}
                    onClick={() => setDebugRole('user')}
                    fullWidth
                  >
                    User
                  </Button>
                  <Button
                    variant={debugRole === 'admin' ? 'contained' : 'outlined'}
                    onClick={() => setDebugRole('admin')}
                    fullWidth
                  >
                    Admin
                  </Button>
                </Stack>
              </Box>
              <Typography variant="body2" color="text.secondary">
                Current status: {user ? `Logged in as ${user.role}` : 'Not logged in'}
              </Typography>
            </Stack>
          </DialogContent>
          <DialogActions>
            <Button onClick={() => setDebugOpen(false)}>Cancel</Button>
            {user ? (
              <Button onClick={handleDebugLogout} color="error">
                Logout
              </Button>
            ) : (
              <Button onClick={handleDebugLogin} color="primary">
                Login as {debugRole}
              </Button>
            )}
          </DialogActions>
        </Dialog>
      )}
    </Box>
  );
};

export default Layout; 