import React, { createContext, useContext, useState, useEffect } from 'react';
import api from '../services/api';

interface GameState {
  currentLevel: number;
  totalPoints: number;
  timeUntilNextAttack: number;
  isEnigmaticPeace: boolean;
  communityProgress: number;
  nextCreatureAttack: string;
}

interface GameStateContextType {
  gameState: GameState | null;
  loading: boolean;
  error: string | null;
  refreshGameState: () => Promise<void>;
}

const GameStateContext = createContext<GameStateContextType | undefined>(undefined);

export const useGameState = () => {
  const context = useContext(GameStateContext);
  if (!context) {
    throw new Error('useGameState must be used within a GameStateProvider');
  }
  return context;
};

export const GameStateProvider: React.FC<{ children: React.ReactNode }> = ({
  children,
}) => {
  const [gameState, setGameState] = useState<GameState | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  const fetchGameState = async () => {
    try {
      const response = await api.get('/game-state');
      setGameState(response.data);
      setError(null);
    } catch (error: any) {
      setError(error.response?.data?.message || 'Error fetching game state');
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchGameState();
  }, []);

  const value = {
    gameState,
    loading,
    error,
    refreshGameState: fetchGameState,
  };

  return (
    <GameStateContext.Provider value={value}>
      {children}
    </GameStateContext.Provider>
  );
};

export default GameStateContext; 